/**
 * SpinToWinPopup Component - Modern & Professional Design
 *
 * A clean, modern gamification popup with:
 * - Minimalist wheel design with subtle gradients
 * - Smooth, professional animations
 * - Clean email capture
 * - Elegant prize reveal
 * - Modern typography and spacing
 * - Professional color palette
 */

import React, { useState, useCallback, useRef, useEffect, useMemo } from 'react';
import { PopupPortal } from './PopupPortal';
import type { PopupDesignConfig, Prize } from './types';
import type { SpinToWinContent } from '~/domains/campaigns/types/campaign';
import { validateEmail, copyToClipboard, prefersReducedMotion } from './utils';

/**
 * SpinToWinConfig - Extends both design config AND campaign content type
 * All content fields (headline, spinButtonText, emailPlaceholder, etc.) come from SpinToWinContent
 * All design fields (colors, position, size, etc.) come from PopupDesignConfig
 */
export interface SpinToWinConfig extends PopupDesignConfig, SpinToWinContent {
  // Storefront-specific fields only
  animationDuration?: number;
  showConfetti?: boolean;

  // Note: wheelSegments, emailRequired, emailPlaceholder, spinButtonText, etc.
  // all come from SpinToWinContent
}

export interface SpinToWinPopupProps {
  config: SpinToWinConfig;
  isVisible: boolean;
  onClose: () => void;
  onSpin?: (email: string) => Promise<void>;
  onWin?: (prize: Prize) => void;
}

export const SpinToWinPopup: React.FC<SpinToWinPopupProps> = ({
                                                                config,
                                                                isVisible,
                                                                onClose,
                                                                onSpin,
                                                                onWin,
                                                              }) => {
  const [email, setEmail] = useState('');
  const [emailError, setEmailError] = useState('');
  const [name, setName] = useState('');
  const [nameError, setNameError] = useState('');
  const [gdprConsent, setGdprConsent] = useState(false);
  const [gdprError, setGdprError] = useState('');
  const [hasSpun, setHasSpun] = useState(false);
  const [isSpinning, setIsSpinning] = useState(false);
  const [wonPrize, setWonPrize] = useState<Prize | null>(null);
  const [rotation, setRotation] = useState(0);
  const [copiedCode, setCopiedCode] = useState(false);
  const [showContent, setShowContent] = useState(false);
  const [emailFocused, setEmailFocused] = useState(false);
  const wheelRef = useRef<SVGSVGElement>(null);

  const wheelSize = config.wheelSize || 380;
  const radius = wheelSize / 2;
  const segments = useMemo(() => config.wheelSegments || [], [config.wheelSegments]);
  const segmentAngle = 360 / Math.max(1, segments.length);
  const accentColor = config.accentColor || config.buttonColor || '#000000';
  const borderRadius = typeof config.borderRadius === 'string'
    ? parseFloat(config.borderRadius) || 16
    : (config.borderRadius ?? 16);
  const animDuration = config.animationDuration ?? 300;

  // Optional extended behavior flags (storefront-only)
  const collectName = config.collectName ?? false;
  const showGdpr = config.showGdprCheckbox ?? false;
  const gdprLabel = config.gdprLabel || 'I agree to receive marketing emails and accept the privacy policy';

  useEffect(() => {
    if (isVisible) {
      const timer = setTimeout(() => setShowContent(true), 50);
      return () => clearTimeout(timer);
    } else {
      setShowContent(false);
    }
  }, [isVisible]);

  // Auto-close timer (migrated from BasePopup)
  useEffect(() => {
    if (!isVisible || !config.autoCloseDelay || config.autoCloseDelay <= 0) return;

    const timer = setTimeout(onClose, config.autoCloseDelay * 1000);
    return () => clearTimeout(timer);
  }, [isVisible, config.autoCloseDelay, onClose]);

  const selectPrize = useCallback((): Prize => {
    const totalProbability = segments.reduce((sum, seg) => sum + seg.probability, 0);
    let random = Math.random() * totalProbability;

    for (const segment of segments) {
      random -= segment.probability;
      if (random <= 0) {
        return segment;
      }
    }

    return segments[0];
  }, [segments]);

  const calculateRotation = useCallback((prizeIndex: number): number => {
    const minSpins = config.minSpins || 5;
    const baseRotation = minSpins * 360;
    const segmentRotation = prizeIndex * segmentAngle;
    const centerOffset = segmentAngle / 2;
    const randomOffset = (Math.random() - 0.5) * (segmentAngle * 0.5);

    return baseRotation + (360 - segmentRotation) + centerOffset + randomOffset;
  }, [segmentAngle, config.minSpins]);

  const handleSpin = useCallback(async () => {
    // Reset previous errors
    setEmailError('');
    setNameError('');
    setGdprError('');

    // Basic guards
    if (config.emailRequired && !email.trim()) {
      setEmailError('Email required');
      return;
    }

    if (config.emailRequired && !validateEmail(email)) {
      setEmailError('Invalid email');
      return;
    }

    if (collectName && !name.trim()) {
      setNameError('Name is required');
      return;
    }

    if (showGdpr && !gdprConsent) {
      setGdprError('You must accept the terms to continue');
      return;
    }

    setIsSpinning(true);

    try {
      if (!config.previewMode && onSpin) {
        await onSpin(email);
      }

      const prize = selectPrize();
      const prizeIndex = segments.findIndex(s => s.id === prize.id);
      const finalRotation = rotation + calculateRotation(prizeIndex);

      setRotation(finalRotation);

      const duration = config.spinDuration || 4000;
      setTimeout(() => {
        setWonPrize(prize);
        setHasSpun(true);
        setIsSpinning(false);

        if (onWin) {
          onWin(prize);
        }
      }, duration);

    } catch (error) {
      console.error('Spin error:', error);
      setEmailError('Error occurred');
      setIsSpinning(false);
    }
  }, [config, email, name, gdprConsent, collectName, showGdpr, onSpin, selectPrize, segments, rotation, calculateRotation, onWin]);

  const handleCopyCode = useCallback(async () => {
    if (wonPrize?.discountCode) {
      const success = await copyToClipboard(wonPrize.discountCode);
      if (success) {
        setCopiedCode(true);
        setTimeout(() => setCopiedCode(false), 2000);
      }
    }
  }, [wonPrize]);

  const renderWheel = () => {
    return segments.map((segment, index) => {
      const startAngle = index * segmentAngle;
      const endAngle = startAngle + segmentAngle;

      const startRad = (startAngle - 90) * (Math.PI / 180);
      const endRad = (endAngle - 90) * (Math.PI / 180);

      const x1 = radius + radius * Math.cos(startRad);
      const y1 = radius + radius * Math.sin(startRad);
      const x2 = radius + radius * Math.cos(endRad);
      const y2 = radius + radius * Math.sin(endRad);

      const largeArc = segmentAngle > 180 ? 1 : 0;
      const pathData = [
        `M ${radius} ${radius}`,
        `L ${x1} ${y1}`,
        `A ${radius} ${radius} 0 ${largeArc} 1 ${x2} ${y2}`,
        'Z'
      ].join(' ');

      // Wheel colors should be preconfigured via admin/theme.
      // Use segment.color when provided, otherwise fall back to accentColor.
      const baseColor = segment.color || accentColor;

      // Check if this is the winning segment
      const isWinningSegment = hasSpun && wonPrize && segment.id === wonPrize.id;
      const strokeColor = isWinningSegment ? '#FFD700' : '#FFFFFF'; // Gold for winner, white for others
      const strokeWidth = isWinningSegment ? 8 : 3; // Thicker border for winner

      const textAngle = startAngle + segmentAngle / 2;
      const textRad = (textAngle - 90) * (Math.PI / 180);
      const textRadius = radius * 0.68;
      const textX = radius + textRadius * Math.cos(textRad);
      const textY = radius + textRadius * Math.sin(textRad);

      return (
        <g key={segment.id}>
          <path
            d={pathData}
            fill={baseColor}
            stroke={strokeColor}
            strokeWidth={strokeWidth}
            style={{
              transition: 'stroke 0.5s ease-out, stroke-width 0.5s ease-out',
            }}
          />
          <text
            x={textX}
            y={textY}
            fill="#FFFFFF"
            fontSize="13"
            fontWeight="600"
            textAnchor="middle"
            dominantBaseline="middle"
            transform={`rotate(${textAngle}, ${textX}, ${textY})`}
            style={{
              pointerEvents: 'none',
              userSelect: 'none',
              fontFamily: '-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, sans-serif',
            }}
          >
            {segment.label}
          </text>
        </g>
      );
    });
  };

  const getInputStyles = (isFocused: boolean, hasError: boolean): React.CSSProperties => ({
    width: '100%',
    padding: '14px 16px',
    fontSize: '15px',
    border: `2px solid ${hasError ? '#EF4444' : isFocused ? accentColor : '#E5E7EB'}`,
    borderRadius: `${borderRadius}px`,
    backgroundColor: '#FFFFFF',
    color: '#111827',
    outline: 'none',
    transition: `all ${animDuration}ms cubic-bezier(0.4, 0, 0.2, 1)`,
    fontFamily: '-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, sans-serif',
  });

  const buttonStyles: React.CSSProperties = {
    width: '100%',
    padding: '16px 32px',
    fontSize: '16px',
    fontWeight: 600,
    border: 'none',
    borderRadius: `${borderRadius}px`,
    backgroundColor: accentColor,
    color: '#FFFFFF',
    cursor: isSpinning ? 'not-allowed' : 'pointer',
    opacity: isSpinning ? 0.6 : 1,
    transition: `all ${animDuration}ms cubic-bezier(0.4, 0, 0.2, 1)`,
    fontFamily: '-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, sans-serif',
  };

  const secondaryButtonStyles: React.CSSProperties = {
    ...buttonStyles,
    backgroundColor: 'transparent',
    color: config.textColor || '#4B5563',
    boxShadow: 'none',
    cursor: 'pointer',
    opacity: 0.9,
  };

  const wheelTransition = prefersReducedMotion()
    ? 'none'
    : `transform ${config.spinDuration || 4000}ms cubic-bezier(0.17, 0.67, 0.12, 0.99)`;

  if (!isVisible) return null;

  return (
    <PopupPortal
      isVisible={isVisible}
      onClose={onClose}
      backdrop={{
        color: config.overlayColor || 'rgba(0, 0, 0, 1)',
        opacity: config.overlayOpacity ?? 0.6,
        blur: 4,
      }}
      animation={{
        type: config.animation || 'fade',
      }}
      position={config.position || 'center'}
      closeOnEscape={config.closeOnEscape !== false}
      closeOnBackdropClick={config.closeOnOverlayClick !== false}
      previewMode={config.previewMode}
      ariaLabel={config.ariaLabel || config.headline}
      ariaDescribedBy={config.ariaDescribedBy}
    >
      <div style={{
        opacity: showContent ? 1 : 0,
        transition: `opacity ${animDuration}ms ease-out`,
      }}>
        <div style={{ display: 'flex', flexDirection: 'column', gap: '32px', alignItems: 'center' }}>
          {/* Headline */}
          <div style={{ textAlign: 'center' }}>
            <h2 style={{
              fontSize: '28px',
              fontWeight: 700,
              margin: '0 0 8px 0',
              lineHeight: 1.3,
              color: config.textColor || '#111827',
              fontFamily: '-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, sans-serif',
            }}>
              {hasSpun && wonPrize ? (
                wonPrize.discountCode
                  ? (config.successMessage?.replace('{{prize}}', wonPrize.label).replace('{{code}}', wonPrize.discountCode) || `You won ${wonPrize.label}!`)
                  : (config.failureMessage || wonPrize.label || 'Thanks for playing!')
              ) : (
                config.headline
              )}
            </h2>
            {!hasSpun && config.subheadline && (
              <p style={{
                fontSize: '16px',
                margin: 0,
                color: config.textColor || '#6B7280',
                lineHeight: 1.5,
                fontFamily: '-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, sans-serif',
              }}>
                {config.subheadline}
              </p>
            )}
          </div>

          {/* Wheel + optional image layout */}
          <div
            style={{
              display: 'flex',
              flexDirection:
                config.imagePosition === 'top' || config.imagePosition === 'bottom'
                  ? 'column'
                  : 'row',
              gap: 24,
              alignItems: 'center',
              justifyContent: 'center',
            }}
          >
            {/* Image block (optional) */}
            {config.imageUrl && config.imagePosition !== 'none' && (
              <div
                style={{
                  flexShrink: 0,
                  order:
                    config.imagePosition === 'right' || config.imagePosition === 'bottom'
                      ? 2
                      : 0,
                  width: 220,
                  height: 220,
                  borderRadius: 24,
                  overflow: 'hidden',
                  backgroundColor: config.imageBgColor || '#0F172A',
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'center',
                  boxShadow: '0 10px 30px rgba(15,23,42,0.4)',
                }}
              >
                <img
                  src={config.imageUrl}
                  alt={config.headline || 'Promotion image'}
                  style={{
                    width: '100%',
                    height: '100%',
                    objectFit: 'cover',
                  }}
                />
              </div>
            )}

            {/* Wheel - always visible, with prize highlight */}
            <div
              style={{
                position: 'relative',
                width: wheelSize,
                height: wheelSize,
              }}
            >
              <svg
                ref={wheelRef}
                width={wheelSize}
                height={wheelSize}
                viewBox={`0 0 ${wheelSize} ${wheelSize}`}
                style={{
                  transform: `rotate(${rotation}deg)`,
                  transition: wheelTransition,
                  filter: hasSpun
                    ? 'drop-shadow(0 18px 45px rgba(15,23,42,0.55))'
                    : 'drop-shadow(0 10px 30px rgba(15,23,42,0.35))',
                }}
              >
                {/* Outer ring / border */}
                <circle
                  cx={radius}
                  cy={radius}
                  r={radius - 4}
                  fill={config.backgroundColor || '#020617'}
                  stroke={config.wheelBorderColor || '#111827'}
                  strokeWidth={config.wheelBorderWidth ?? 6}
                />

                {renderWheel()}

                {/* Center button */}
                <circle
                  cx={radius}
                  cy={radius}
                  r={40}
                  fill={accentColor}
                  stroke="rgba(15,23,42,0.85)"
                  strokeWidth={4}
                />
                <circle
                  cx={radius}
                  cy={radius}
                  r={12}
                  fill="#F9FAFB"
                  stroke="rgba(15,23,42,0.3)"
                  strokeWidth={2}
                />
                <text
                  x={radius}
                  y={radius}
                  textAnchor="middle"
                  dominantBaseline="middle"
                  fill="#F9FAFB"
                  fontSize="12"
                  fontWeight="600"
                  style={{
                    letterSpacing: '0.12em',
                    textTransform: 'uppercase',
                    fontFamily:
                      '-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, sans-serif',
                  }}
                >
                  SPIN
                </text>
              </svg>

              {/* Pointer */}
              <div
                style={{
                  position: 'absolute',
                  top: -22,
                  left: '50%',
                  transform: 'translateX(-50%)',
                  width: 0,
                  height: 0,
                  borderLeft: '16px solid transparent',
                  borderRight: '16px solid transparent',
                  borderTop: `30px solid ${accentColor}`,
                  filter: 'drop-shadow(0 4px 10px rgba(15,23,42,0.5))',
                  zIndex: 10,
                }}
              />
            </div>
          </div>

          {/* Email input or Prize details */}
          {!hasSpun ? (
            <>
              {/* Optional Name field */}
              {collectName && (
                <div style={{ width: '100%', maxWidth: '400px' }}>
                  <label
                    style={{
                      display: 'block',
                      marginBottom: '8px',
                      fontSize: '14px',
                      fontWeight: 600,
                      color: config.textColor || '#374151',
                      fontFamily: '-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, sans-serif',
                    }}
                  >
                    Name
                  </label>
                  <input
                    type="text"
                    value={name}
                    onChange={(e) => {
                      setName(e.target.value);
                      if (nameError) setNameError('');
                    }}
                    placeholder="Enter your name"
                    style={getInputStyles(false, !!nameError)}
                    disabled={isSpinning}
                  />
                  {nameError && (
                    <p
                      style={{
                        color: '#EF4444',
                        fontSize: '13px',
                        margin: '6px 0 0 0',
                        fontFamily: '-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, sans-serif',
                      }}
                    >
                      {nameError}
                    </p>
                  )}
                </div>
              )}

              {/* Email input - clean design */}
              {config.emailRequired && (
                <div style={{ width: '100%', maxWidth: '400px' }}>
                  {config.emailLabel && (
                    <label style={{
                      display: 'block',
                      marginBottom: '8px',
                      fontSize: '14px',
                      fontWeight: 600,
                      color: config.textColor || '#374151',
                      fontFamily: '-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, sans-serif',
                    }}>
                      {config.emailLabel}
                    </label>
                  )}
                  <input
                    type="email"
                    value={email}
                    onChange={(e) => {
                      setEmail(e.target.value);
                      if (emailError) setEmailError('');
                    }}
                    onFocus={() => setEmailFocused(true)}
                    onBlur={() => setEmailFocused(false)}
                    placeholder={config.emailPlaceholder || 'your@email.com'}
                    style={getInputStyles(emailFocused, !!emailError)}
                    disabled={isSpinning}
                  />
                  {emailError && (
                    <p style={{
                      color: '#EF4444',
                      fontSize: '13px',
                      margin: '6px 0 0 0',
                      fontFamily: '-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, sans-serif',
                    }}>
                      {emailError}
                    </p>
                  )}
                </div>
              )}

              {/* Optional GDPR checkbox */}
              {showGdpr && (
                <div style={{ width: '100%', maxWidth: '400px' }}>
                  <div style={{ display: 'flex', alignItems: 'flex-start', gap: '10px', marginTop: '8px' }}>
                    <input
                      id="spin-gdpr"
                      type="checkbox"
                      checked={gdprConsent}
                      onChange={(e) => {
                        setGdprConsent(e.target.checked);
                        if (gdprError) setGdprError('');
                      }}
                      style={{
                        width: '16px',
                        height: '16px',
                        marginTop: '2px',
                        cursor: 'pointer',
                      }}
                      disabled={isSpinning}
                    />
                    <label
                      htmlFor="spin-gdpr"
                      style={{
                        fontSize: '13px',
                        lineHeight: 1.5,
                        color: config.textColor || '#4B5563',
                        cursor: 'pointer',
                        fontFamily: '-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, sans-serif',
                      }}
                    >
                      {gdprLabel}
                    </label>
                  </div>
                  {gdprError && (
                    <p
                      style={{
                        color: '#EF4444',
                        fontSize: '13px',
                        margin: '6px 0 0 0',
                        fontFamily: '-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, sans-serif',
                      }}
                    >
                      {gdprError}
                    </p>
                  )}
                </div>
              )}

              {/* Clean spin button */}
              <button
                onClick={handleSpin}
                disabled={isSpinning}
                style={buttonStyles}
                onMouseEnter={(e) => {
                  if (!isSpinning) {
                    e.currentTarget.style.transform = 'translateY(-1px)';
                  }
                }}
                onMouseLeave={(e) => {
                  e.currentTarget.style.transform = 'translateY(0)';
                }}
              >
                {isSpinning ? (
                  <span style={{ display: 'flex', alignItems: 'center', justifyContent: 'center', gap: '10px' }}>
                    <span style={{
                      width: '16px',
                      height: '16px',
                      border: '2px solid rgba(255,255,255,0.3)',
                      borderTopColor: '#FFF',
                      borderRadius: '50%',
                      animation: 'spin 0.8s linear infinite'
                    }} />
                    {config.loadingText || 'Spinning...'}
                  </span>
                ) : (
                  config.spinButtonText || config.buttonText || 'Spin the Wheel'
                )}
              </button>

              <button
                type="button"
                onClick={onClose}
                style={{
                  ...secondaryButtonStyles,
                  marginTop: '8px',
                }}
                onMouseEnter={(e) => (e.currentTarget.style.opacity = '1')}
                onMouseLeave={(e) => (e.currentTarget.style.opacity = '0.9')}
              >
                {config.dismissLabel || 'No thanks'}
              </button>
            </>
          ) : (
            // Prize details - shown below the wheel
            wonPrize?.discountCode && (
              <div style={{
                width: '100%',
                maxWidth: '400px',
                marginTop: '8px',
                padding: '24px',
                backgroundColor: '#F9FAFB',
                borderRadius: `${borderRadius}px`,
                border: '1px solid #E5E7EB',
                animation: 'slideUp 0.5s ease-out',
              }}>
                <p style={{
                  fontSize: '13px',
                  margin: '0 0 12px 0',
                  color: '#6B7280',
                  fontWeight: 600,
                  textTransform: 'uppercase',
                  letterSpacing: '0.5px',
                  fontFamily: '-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, sans-serif',
                  textAlign: 'center',
                }}>
                  Your Discount Code
                </p>
                <div style={{
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'center',
                  gap: '12px',
                  flexWrap: 'wrap',
                }}>
                  <code style={{
                    fontSize: '28px',
                    fontWeight: 700,
                    padding: '12px 24px',
                    backgroundColor: '#FFFFFF',
                    borderRadius: `${borderRadius - 4}px`,
                    letterSpacing: '2px',
                    color: accentColor,
                    border: '2px solid #E5E7EB',
                    fontFamily: 'SF Mono, Monaco, Consolas, monospace',
                  }}>
                    {wonPrize.discountCode}
                  </code>
                  <button
                    onClick={handleCopyCode}
                    style={{
                      padding: '12px 20px',
                      fontSize: '14px',
                      fontWeight: 600,
                      border: `2px solid ${copiedCode ? '#10B981' : '#E5E7EB'}`,
                      borderRadius: `${borderRadius - 4}px`,
                      backgroundColor: copiedCode ? '#10B981' : '#FFFFFF',
                      color: copiedCode ? '#FFFFFF' : '#374151',
                      cursor: 'pointer',
                      transition: `all ${animDuration}ms`,
                      fontFamily: '-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, sans-serif',
                    }}
                  >
                    {copiedCode ? 'Copied!' : 'Copy'}
                  </button>
                </div>
                {wonPrize.discountValue && (
                  <p style={{
                    fontSize: '15px',
                    margin: '16px 0 0 0',
                    color: '#374151',
                    fontWeight: 500,
                    fontFamily: '-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, sans-serif',
                    textAlign: 'center',
                  }}>
                    {wonPrize.discountType === 'percentage' && `Save ${wonPrize.discountValue}%`}
                    {wonPrize.discountType === 'fixed_amount' && `Save $${wonPrize.discountValue}`}
                    {wonPrize.discountType === 'free_shipping' && 'Free Shipping'}
                  </p>
                )}
              </div>
            )
          )}
        </div>
      </div>

      <style>{`
        @keyframes spin {
          to { transform: rotate(360deg); }
        }

        @keyframes slideUp {
          from {
            opacity: 0;
            transform: translateY(20px);
          }
          to {
            opacity: 1;
            transform: translateY(0);
          }
        }
      `}</style>
    </PopupPortal>
  );
};
